import {versionCompare} from "../../utils/versioncompare";
import {BasewndVersion_4_0_0} from "./basewndVersion_4_0_0";
import {BasewndVersion_3_2_0} from "./basewndVersion_3_2_0";
import {BasewndVersion_3_1_0} from "./basewndVersion_3_1_0";
import {BasewndVersion_1_0_0} from "./basewndVersion_1_0_0";
import {ContextInterface} from '../../interfaces';

/**
 * Helper Wrapper Class
 * Check version and return versioned class instance
 */
export class BasewndEngine {
    constructor(context: ContextInterface, version: string) {

        if (versionCompare(version, '4.0.0', '>=')) {
            return new BasewndVersion_4_0_0(context);
        } else if (version === '3.2.0') {
            return new BasewndVersion_3_2_0(context);
        } else if (version === '3.1.0') {
            return new BasewndVersion_3_1_0(context);
        } else {
            return new BasewndVersion_1_0_0(context);
        }

    }
}
