/**
 * Callback object helps to communicate with C++ async download
 * C++ expect IDispatch type object that will be used for notifying caller about download progress.
 */
export interface ClientCallbackInterface {
    onProgress?(progress: string): any;
    onError?(): any;
    onCancelled?(): any;
    onFinished?(downloadID: string): any;
}

export class CallbackObject {

    onProgress = (progress: string): any => {
        console.log(`Default progress: ${progress}`);
    };

    onError = (): any => {
        console.error('Default error occurred');
    };

    onCancelled = (): any => {
        console.log('Download was cancelled');
    };

    onFinished = (downloadID: string): any => {
        console.log(`Download with ID ${downloadID} has finished`);
    };
}
